body {
  overflow-y: hidden;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
  font-family: "Poppins";
  src: url("./fonts/Poppins-Regular.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}

.containertask {
  display: flex;
  max-width: 100vw;
  width: 100% !important;
  height: 70px;
  box-sizing: border-box;
  align-items: center;
  gap: 44.11%;
  background: #ffffff;
  box-shadow: 0px 5px 25px rgba(0, 0, 0, 0.1);
}
#todocontain {
  display: flex;
  align-items: center;
  box-sizing: border-box;
  padding-left: 14.3%;
  
}
#Title2 {
  height: 53px;
  width: 170px;
  object-fit: contain;
}

#logoutdiv {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 30%;
}
#IconProfile {
  width: 42px;
  height: 42px;
  position: absolute;
  left: 80%;
  cursor: pointer;
  user-select: none;
}

#Logoutpng {
  width: 52px;
  height: 17px;
  user-select: none;
  cursor: pointer;
}

#ProfileDialogActions {
  display: flex;
  justify-content: flex-start;
  padding-left: 0;
}
#Dialogger {
  padding: 0;
}

#Sidebar {
  background-color: #060652;
  width: 21%;
  height: 1920px;
}

#container1 {
  display: flex;
}

#root {
  height: 1920px;
  overflow-y: hidden;
}

#options{
    color: #ffffff;
    font-family: "Poppins";
    font-size: larger;
    margin-left: 14%;
    margin-top: 30px;
}


#Tittle{
  color: black;
  font-family: "Poppins";
  font-size: larger;
  font-weight: 600;

}

#options2{
    color: #ffffff;
    font-family: "Poppins";
    font-size: larger;
    user-select: none;
}
#optioncontainer{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 70px;
  cursor: pointer;
}
#optioncontainer:hover{
  background-color: aquamarine;
  animation: 0s;
}

#alloptionconatainer{
  display: flex;
  margin-top: 12%;
  flex-direction: column;
}